import { http } from '@/http';
import Toast from "vant/es/toast";
import "vant/es/toast/style/less";
import { defineComponent, onMounted, ref } from 'vue';
export default defineComponent({
    name: 'SellerFans',
    setup() {
        const fansNum = ref(''); // 粉丝数目
        const scanCodeNum = ref(''); // 扫烟码数
        const shopScanActNum = ref(''); // 核销数量
        const fansList = ref([]);
        const getShopData = () => {
            http
                .post('/hbSeller/sellerFans/shopStatis', {}, false)
                .then((res) => {
                if (res.code === '200') {
                    if (res.data) {
                        fansNum.value = res.data.fansNum || 0;
                        scanCodeNum.value = res.data.scanCodeNum || 0;
                        shopScanActNum.value = res.data.shopScanActNum || 0;
                    }
                    else {
                        fansNum.value = '0';
                        scanCodeNum.value = '0';
                        shopScanActNum.value = '0';
                    }
                }
                else {
                    Toast.fail(res.msg);
                }
            })
                .catch((err) => {
                console.log(err);
            });
        };
        const getFansList = () => {
            http
                .post('/hbSeller/sellerFans/fanList', {}, false)
                .then((res) => {
                if (res.code === '200') {
                    fansList.value = res.data;
                }
                else {
                    Toast.fail(res.msg);
                }
            })
                .catch((err) => {
                console.log(err);
            });
        };
        onMounted(() => {
            getShopData();
            getFansList();
        });
        return {
            fansNum,
            scanCodeNum,
            shopScanActNum,
            fansList
        };
    }
});
